/* eslint-disable max-len */
/* eslint-disable no-param-reassign */
const ADD_POST = 'ADD-POST';
const UPDATE_NEW_POST_TEXT = 'UPDATE-NEW-POST-TEXT';

const initialState = {
  postsData: [
    { id: 1, message: 'hi, how are you?', likesCount: '15' },
    { id: 2, message: 'Hi!', likesCount: '15' },
    { id: 3, message: "it's my first post", likesCount: '50' },
    { id: 4, message: 'Hi!', likesCount: '20' },
    { id: 5, message: 'Hi!', likesCount: '300' },
    { id: 6, message: 'what is your name?', likesCount: '123' },
    { id: 7, message: 'Hi!', likesCount: '79' },
    { id: 8, message: 'Hi!', likesCount: '26' },
  ],
  newPostText: '',
};

const profileReducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_POST: {
      const newPost = {
        id: 9,
        message: state.newPostText,
        likesCount: '0',
      };
      return {
        ...state,
        postsData: [...state.postsData, newPost],
        newPostText: '',
      };
    }
    case UPDATE_NEW_POST_TEXT: {
      return { ...state, newPostText: action.newText };
    }
    default: {
      return state;
    }
  }
};

// to prevent errors in writing type and properties
export const addPostActionCreator = () => ({ type: ADD_POST });
export const updateNewPostTextActionCreator = (text) => ({ type: UPDATE_NEW_POST_TEXT, newText: text });

export default profileReducer;
