/* eslint-disable max-len */
/* eslint-disable react/prop-types */
/* eslint-disable react/destructuring-assignment */
import React from 'react';
import classes from './Messages.module.css';
import DialogItem from './DialogItem/DialogItem';
import Message from './MessageItem/MessagesDialog';

const Messages = (props) => {
  const state = props.messagesPage;
  const dialogsElements = state.dialogsData.map((dialog) => <DialogItem name={dialog.name} key={dialog.id} id={dialog.id} />);
  const messagesElements = state.messagesData.map((messageDialog) => <Message message={messageDialog.message} key={messageDialog.id} />);

  const newMessagesElement = React.createRef();

  const onSendMessageClick = () => props.sendMessage();

  const onNewMessageChange = (e) => {
    const body = e.target.value;
    props.updateNewMessageText(body);
  };
  return (
    <div>
      <div className={classes.dialogs}>
        <div className={classes.dialogItems}>
          { dialogsElements }
        </div>
        <div className={classes.messages}>
          { messagesElements }
        </div>
      </div>
      <div className={classes.textarea}>
        <div><textarea placeholder="Enter your message" onChange={onNewMessageChange} ref={newMessagesElement} value={props.newMessageText} /></div>
        <div><button type="button" className={classes.addMessage} onClick={onSendMessageClick}>Add Message</button></div>
      </div>
    </div>
  );
};

export default Messages;
