/* eslint-disable no-debugger */
/* eslint-disable no-param-reassign */
/* eslint-disable max-len */
const ADD_MESSAGE = 'ADD-MESSAGE';
const UPDATE_NEW_MESSAGE_TEXT = 'UPDATE-NEW-MESSAGE-TEXT';

const initialState = {
  dialogsData: [
    { id: 1, name: 'Alex' },
    { id: 2, name: 'Nikita' },
    { id: 3, name: 'Mary' },
    { id: 4, name: 'July' },
    { id: 5, name: 'Valera' },
    { id: 6, name: 'Vlad' },
    { id: 7, name: 'Serge' },
    { id: 8, name: 'Linda' },
  ],
  messagesData: [
    { id: 1, message: 'Hi!' },
    { id: 2, message: 'How are you?' },
    { id: 3, message: 'Yo!' },
    { id: 4, message: 'Ok!' },
    { id: 5, message: 'I like juice' },
    { id: 6, message: 'Do you like an ice-cream?"' },
    { id: 7, message: 'Foo!' },
    { id: 8, message: 'Are you crazy!!!' },
  ],
  newMessageText: '',
};

const messagesReducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_MESSAGE: {
      const body = state.newMessageText;
      return {
        ...state,
        newMessageText: '',
        messagesData: [...state.messagesData, { id: 9, message: body }],
      };
    }
    case UPDATE_NEW_MESSAGE_TEXT:
      return {
        ...state,
        newMessageText: action.text,
      };
    default: {
      return state;
    }
  }
};

export const addMessageActionCreator = () => ({ type: ADD_MESSAGE });
export const updateNewMessageTextActionCreator = (text) => ({ type: UPDATE_NEW_MESSAGE_TEXT, text });

export default messagesReducer;
