const ADD_NEW_FRIENDS = 'ADD-NEW-FRIENDS';
const initialState = {
  friends: [
    { id: 1, name: 'Andrew' },
    { id: 2, name: 'Sasha' },
    { id: 3, name: 'Sven' },
  ],
};
const sidebarReducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_NEW_FRIENDS: {
      const newFriend = {
        id: 4, name: '',
      };
      state.friends.push(newFriend);
      return state;
    }
    default: {
      return state;
    }
  }
};

export const addNewFriends = () => ({ type: ADD_NEW_FRIENDS });
export default sidebarReducer;
